@import url('https://fonts.googleapis.com/css2?family=Londrina+Outline&display=swap');

.top-link {
    display: 'inline-block';
    align-items: flex-start
}

.logo {
    font-family: 'Londrina Outline';
    color: white;
    font-weight: '400';
    font-size: 2.5em;
}

.logo a,
.logo a:hover,
.logo a:active,
.logo a:visited {
    color: white;
    text-decoration: none;
}


#mailing-list:hover,
#chat:hover {
    color:#ffccbc
}

.top-link:hover,
.top-link:active {
    color:#ffccbc
}

.top-link:visited {
    color: white;
}